.field-group__heading {
  display: block; //     span    display: inline;        
  margin-bottom: 35px;

  span {
    position: relative; //   z-index     before (  )
    z-index: 1;
    background-color: $white;
    width: max-content;
    padding-left: 22px;
    padding-right: 22px;
    margin-left: -22px;
    display: flex; //            (         ) 
  }
  
  font-family: $font-heading;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;
}

.field-group__heading::before { //    before,             after!!!      position: relative;       margin-top;           field-group__heading
  position: absolute; //          static       html
  content: "";
  height: 2px;
  left: 0;
  right: 0;
  margin-top: 16px;
  background-color: $color-primary;
}

.field-group__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: grid;
  gap: 14px;

  &--contacts {
    @media (min-width: $desktop-width) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 80px;
    }
  }
  &--additional{
    @media (min-width: $tablet-width) {
      grid-template-columns: 1fr 1fr; 
      row-gap: 32px;
      column-gap: 60px;
    }
    @media (min-width: $desktop-width) {
      grid-template-columns: repeat(4, 1fr);
      column-gap: 80px;
    }
  }
}

.field__lead,
.field__input {
  font-family: $font-heading;
  color: $special-dark-grey;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
}

.field__input::placeholder {
  color: $special-dark-grey;
}

